.visually-hidden {
  position: absolute;
  top: 0;
  left: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.list {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: flex-start;
  list-style: none; }
  .list__item {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    max-width: 270px;
    width: 100%;
    border-radius: 5px;
    border: 1px solid #eeeeee;
    margin-right: 30px;
    padding: 20px;
    list-style: none; }
    .list__item img {
      margin-bottom: 15px; }
    .list__item span {
      color: #888888;
      font-size: 13px;
      line-height: 19px;
      margin-bottom: 5px;
      font-weight: 300; }
    .list__item h3 {
      font-size: 15px;
      line-height: 18px;
      margin-bottom: 5px;
      font-weight: lighter; }
  .list__item:last-of-type {
    margin-right: 0; }
  .list__wrapper {
    position: relative; }
  .list__tags {
    position: absolute;
    display: flex;
    justify-content: flex-start; }
  .list__rating {
    display: flex;
    flex-direction: flex-start;
    align-items: top;
    margin-bottom: 18px; }
    .list__rating img {
      margin-right: 5px; }
    .list__rating span {
      display: inline-block;
      color: #00adef;
      font-size: 13px;
      line-height: 15px;
      margin-top: 0;
      font-weight: 300; }
  .list__prices {
    display: flex;
    justify-content: space-between;
    align-items: bottom;
    font-size: 19px;
    line-height: 23px;
    margin-bottom: 14px; }
    .list__prices p {
      font-weight: lighter; }
    .list__prices s {
      font-size: 13px;
      line-height: 15px;
      color: #888888;
      font-weight: lighter; }
    .list__prices p:last-of-type {
      margin-right: 12px; }
  .list__buttons {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .list__buttons svg {
      position: relative;
      right: 3px; }
    .list__buttons svg:hover,
    .list__buttons svg:focus {
      transition: all 0.6s;
      cursor: pointer; }
      .list__buttons svg:hover path,
      .list__buttons svg:focus path {
        transition: all 0.6s;
        fill: #00adef; }
    .list__buttons svg.active path {
      fill: #00adef;
      fill-rule: nonzero;
      clip-rule: nonzero; }

@media (max-width: 769px) {
  .list__item {
    max-width: 247px; }
  .list__wrapper img {
    max-width: 209px;
    height: auto;
    margin-bottom: 28px; }
  .list__prices s {
    display: none; } }

@media (max-width: 500px) {
  .list__item {
    min-width: 290px;
    position: relative;
    padding: 15px; }
    .list__item h3 {
      font-size: 13px;
      line-height: 15px; }
  .list__wrapper img {
    max-height: 100px;
    width: auto;
    margin: 0 auto;
    margin-bottom: 17px; }
  .list__tags {
    max-width: 70%;
    display: block;
    top: 4px;
    left: 4px; }
  .list__rating {
    margin-bottom: 6px; }
  .list__prices {
    margin-bottom: 16px; }
    .list__prices p {
      font-size: 15px;
      line-height: 18px; }
  .list__buttons svg {
    position: absolute;
    top: 18px;
    right: 17px; }
  .list__buttons svg:first-of-type {
    top: 16px;
    right: 50px; } }

.tag {
  display: inline-block;
  font-size: 13px;
  line-height: 15px;
  box-sizing: border-box;
  padding: 5px 10px;
  border-radius: 5px;
  margin-right: 5px;
  font-weight: lighter; }
  .tag--new {
    background-color: #eefcf6;
    color: #3ddc97; }
  .tag--hit {
    background-color: #fcf4ed;
    color: #e49f62; }

@media (max-width: 769px) {
  .tag--new {
    display: none; } }

@media (max-width: 500px) {
  .tag--hit, .tag--new {
    margin-bottom: 5px;
    display: inline-block; } }

.content {
  display: block;
  max-width: 1170px;
  width: 100%;
  margin: 0 auto; }
  .content__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    margin-bottom: 25px; }
    .content__head h2 {
      font-weight: lighter;
      font-size: 31px;
      line-height: 37px;
      position: relative; }
      .content__head h2::after {
        content: '';
        display: inline-block;
        position: absolute;
        width: 5px;
        height: 5px;
        border: 2px solid #444444;
        transform: rotate(45deg);
        border-left: none !important;
        border-bottom: none !important;
        top: 40%;
        right: -23px; }

@media (max-width: 500px) {
  .content__head {
    margin-bottom: 15px; }
    .content__head h2 {
      font-size: 27px;
      line-height: 33px;
      display: inline-block;
      width: 100%; }
      .content__head h2::after {
        right: 10px; }
  .content__arrows {
    display: none; } }

.arrow {
  display: inline-block;
  width: 24px;
  height: 24px;
  border-radius: 24px;
  background-color: #f6f6f6;
  border: none;
  margin-right: 2px;
  position: relative;
  padding: 0;
  box-sizing: border-box; }
  .arrow::after {
    content: '';
    display: inline-block;
    position: absolute;
    width: 6px;
    height: 6px;
    border: 2px solid #888888;
    transform: rotate(45deg);
    border-left: none !important;
    border-bottom: none !important;
    top: 8px;
    left: 8px; }
  .arrow--left::after {
    transform: rotate(-135deg); }
  .arrow:hover, .arrow:focus {
    cursor: pointer;
    opacity: 0.6;
    transition: all 0.6s; }

.arrow:last-of-type {
  margin-right: 0; }

.main {
  box-sizing: border-box;
  padding: 25px 55px;
  overflow-x: hidden; }

picture {
  display: flex; }

@media (max-width: 769px) {
  .main {
    padding: 18px 20px; } }

@media (max-width: 500px) {
  .main {
    padding: 18px 15px; } }

.button {
  background-color: #00adef;
  color: #ffffff;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  min-height: 48px;
  max-width: 152px;
  width: 100%;
  padding: 14px;
  border-radius: 5px;
  transition: all 0.4s;
  font-size: 15px;
  line-height: 18px; }
  .button:hover, .button:focus {
    cursor: pointer;
    background-color: #0076a3; }
  .button:active {
    opacity: 0.4; }
  .button:disabled {
    opacity: 0.1;
    pointer-events: none; }

@media (max-width: 769px) {
  .button {
    max-width: 131px; } }

@media (max-width: 500px) {
  .button {
    max-width: 100%; } }
